/*-----------------------------------------------------------------------------------*/
/*  Menu Header DESKTOP
/*-----------------------------------------------------------------------------------*/
#header-top {
  height: 110px;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  top: 0;
  width: 100%;
  z-index: 999;
  background-color: pueple; }
  @media (max-width: 768px) {
    #header-top {
      top: 0;
      transition: all 0.5s;
      position: fixed;
      box-shadow: 0 5px 0 rgba(0, 0, 0, 0.1);
      width: 100%;
      background-color: #fff;
      display: flex; } }
  #header-top .logo {
    max-width: 200px; }
    #header-top .logo img {
      transition: all 0.6s;
      padding: 0px; }
      @media (max-width: 768px) {
        #header-top .logo img {
          scale: 0.8; } }
  #header-top li {
    margin-right: 1rem; }
  #header-top li:last-child,
  #header-top li:nth-last-child(2) {
    margin-right: 0.5rem;
    margin-left: 0.5rem; }
  #header-top .container {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-direction: row;
    padding: 0px !important;
    justify-content: space-between; }
    #header-top .container .show .sub-menu {
      display: flex;
      position: absolute;
      height: auto;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      padding: 32px 30px 37px;
      top: 100%;
      border: none;
      background-color: #fff;
      margin-top: 24px;
      z-index: 999; }
      #header-top .container .show .sub-menu > a {
        color: black; }
    #header-top .container .menu-item-has-children {
      position: relative; }
      #header-top .container .menu-item-has-children:after {
        content: '❯';
        font-size: 0.7rem;
        transform: rotate(90deg);
        position: absolute;
        right: 5%;
        transition: all 0.5s;
        color: #664fb7; }
        @media (max-width: 992px) {
          #header-top .container .menu-item-has-children:after {
            right: 0%; } }
    #header-top .container .show > a {
      color: #5dc1c3; }
    #header-top .container .show .sub-menu > a {
      color: black; }
    #header-top .container .show:after {
      color: #5dc1c3;
      transform: rotate(270deg); }
    #header-top .container .reques-demo:hover a {
      color: white; }
    #header-top .container .sign_in {
      background-color: #664fb7;
